//     '/monaco/about': () => <ProjectPage content={Monaco} backURL={'/about/skip'} />,
//     '/taggle': () => <ProjectPage content={Taggle} backURL={'/static'} />,
//     '/capsule': () => <ProjectPage content={Capsule} backURL={'/static'} />,
//     '/capsule/sidework': () => <ProjectPage content={Capsule} backURL={'/sidework/skip'} />,
//     '/atomichabits': () => <ProjectPage content={AtomicHabits} imageColorHoverFilter={imageHoverResult.filter} backURL={'/static'} />,
//     '/nuhub': () => <ProjectPage content={NuHub} backURL={'/static'} />,
//     '/dailyui': () => <ProjectPage content={DailyUi} backURL={'/static'} />,
//     '/dailyui/about': () => <ProjectPage content={DailyUi} backURL={'/about/skip'} />,
// };

// function App() {
//     const routeResult = useRoutes(routes) || <NotFoundPage />;
//     return routeResult;
// }

const App = () => (
    <BrowserRouter>
        <div>
            <Route exact path="/" render={(props) => <IntroPage {...props} />}/>
            <Route exact path="/skip" component={() => <IntroPage skipIntro={true} />} />
            <Route exact path="/work" component={() => <WorkPage />} />
            <Route exact path="/work/skip" component={() => <WorkPage skipIntro={true}  />} />
            <Route exact path="/sidework" component={() => <SideWorkPage imageColorFilter={imageResult.filter} imageColorHoverFilter={imageHoverResult.filter} />} />
            <Route exact path="/sidework/skip" component={() => <SideWorkPage imageColorFilter={imageResult.filter} imageColorHoverFilter={imageHoverResult.filter} skipIntro={true} />} />
            <Route exact path="/about" component={() => <About imageColorFilter={imageResult.filter} imageColorHoverFilter={imageHoverResult.filter} skipIntro={true} />} />
            <Route exact path="/about/skip" component={() => <About imageColorFilter={imageResult.filter} imageColorHoverFilter={imageHoverResult.filter} skipIntro={true} />} />
            <Route exact path="/conclusion" component={() => <Conclusion />} />

            <Route exact path="/static" component={() => <MainHome imageColorFilter={imageResult.filter} imageColorHoverFilter={imageHoverResult.filter} />} />
            <Route exact path="/deeplocal" component={() => <ProjectPage content={Deeplocal} backURL={'/static'} />} />
            <Route exact path="/deeplocal/work" component={() => <ProjectPage content={Deeplocal} backURL={'/work/skip'} />} />
            <Route exact path="/monaco" component={() => <ProjectPage content={Monaco} backURL={'/static'} />} />
            <Route exact path="/monaco/about" component={() => <ProjectPage content={Monaco} backURL={'/about/skip'} />} />
            <Route exact path="/taggle" component={() => <ProjectPage content={Taggle} backURL={'/static'} />} />
            <Route exact path="/capsule" component={() => <ProjectPage content={Capsule} backURL={'/static'} />} />
            <Route exact path="/capsule/sidework" component={() => <ProjectPage content={Capsule} backURL={'/sidework/skip'} />} />
            <Route exact path="/atomichabits" component={() => <ProjectPage content={AtomicHabits} imageColorHoverFilter={imageHoverResult.filter} backURL={'/static'} />} />
            <Route exact path="/nuhub" component={() => <ProjectPage content={NuHub} backURL={'/static'} />} />
            <Route exact path="/dailyui" component={() => <ProjectPage content={DailyUi} backURL={'/static'} />} />
            <Route exact path="/dailyui/about" component={() => <ProjectPage content={DailyUi} backURL={'/about/skip'} />} />
        </div>
    </BrowserRouter>
  )

ReactDOM.render(<App />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
